import Vue from "vue";
import Router, { RouterOptions } from "vue-router";

Vue.use(Router);

const baseRoutes = [
  {
    path: "*",
    name: "404",
    component: Vue.component("error404", {
      created() {
        qTouch.navigateToUrl(qTouch.appPathMap.errorPage + "/404");
      }
    })
  }
];

const routeConfig: RouterOptions = {
  mode: "history",
  base: qTouch.basePath + qTouch.appPathMap.buildingManage,
  routes: [
    {
      path: "/building",
      name: "buildingList",
      component: () => import("../views/buildingManage/BuildingManage.vue")
    },
    {
      path: "/buildingCustomProps",
      name: "buildingTypeList",
      component: () => import("../views/buildingTypeManage/BuildingTypeManage.vue")
    },
    ...baseRoutes
  ]
};

export default routeConfig;
